// API
// API

class ApiAdapter {
  constructor(baseUrl, options = {}) {
    this.baseUrl = baseUrl;
    this.options = {
      maxRetries: 3,
      retryDelay: 1000,
      timeout: 10000,
      ...options
    };
  }

  /**
   * HTTP
   * @param {string} endpoint - API
   * @param {Object} options - 
   * @param {number} retryCount - 
   * @returns {Promise<Object>} - 
   */
  async request(endpoint, options = {}, retryCount = 0) {
    const url = endpoint.startsWith('http') ? endpoint : `${this.baseUrl}${endpoint}`;
    
    const fetchOptions = {
      ...options,
      headers: {
        'Content-Type': 'application/json',
        ...options.headers
      },
      signal: AbortSignal.timeout(this.options.timeout)
    };

    try {
      const response = await fetch(url, fetchOptions);
      
      if (!response.ok) {
        throw new Error(`API request failed with status ${response.status}: ${response.statusText}`);
      }
      
      return await response.json();
    } catch (error) {
      // 
      if (error.name === 'AbortError') {
        console.error(`Request timeout for ${url}`);
      } else {
        console.error(`Request error for ${url}:`, error);
      }
      
      // 
      if (retryCount < this.options.maxRetries) {
        const delay = this.options.retryDelay * Math.pow(2, retryCount);
        console.log(`Retrying request to ${url} in ${delay}ms (${retryCount + 1}/${this.options.maxRetries})`);
        
        await new Promise(resolve => setTimeout(resolve, delay));
        return this.request(endpoint, options, retryCount + 1);
      }
      
      throw error;
    }
  }

  /**
   * API
   * @param {Error} error - 
   * @param {string} source - 
   * @returns {Object} - 
   */
  handleError(error, source) {
    const errorInfo = {
      message: error.message || 'Unknown error occurred',
      source: source,
      timestamp: new Date().toISOString(),
      isRetryable: this.isRetryableError(error)
    };
    
    console.error(`API Error [${source}]:`, errorInfo);
    
    return errorInfo;
  }

  /**
   * 
   * @param {Error} error - 
   * @returns {boolean} - 
   */
  isRetryableError(error) {
    // 
    if (error.name === 'AbortError') return true;
    if (error.message && error.message.includes('network')) return true;
    
    // HTTP
    if (error.status) {
      // 5xx
      return error.status >= 500 && error.status < 600;
    }
    
    return false;
  }

  /**
   * API
   * 
   * @param {Object} apiJob - API
   * @returns {Object} - 
   */
  transformJob(apiJob) {
    throw new Error('transformJob method must be implemented by subclass');
  }

  /**
   * API
   * 
   * @param {Object} apiJobDetail - API
   * @returns {Object} - 
   */
  transformJobDetail(apiJobDetail) {
    throw new Error('transformJobDetail method must be implemented by subclass');
  }
}

export default ApiAdapter;