// 
// 

class CacheService {
  constructor(options = {}) {
    this.options = {
      memoryTTL: 5 * 60 * 1000, // 5
      storageTTL: 60 * 60 * 1000, // 1
      prefix: 'job_api_cache_',
      ...options
    };
    
    // 
    this.memoryCache = new Map();
    
    // 
    this.stats = {
      hits: 0,
      misses: 0,
      memoryHits: 0,
      storageHits: 0
    };
  }

  /**
   * 
   * @param {string} key - 
   * @returns {string} - 
   */
  getCacheKey(key) {
    return `${this.options.prefix}${key}`;
  }

  /**
   * 
   * @param {string} key - 
   * @returns {Promise<any>} - null
   */
  async get(key) {
    const cacheKey = this.getCacheKey(key);
    
    // 
    if (this.memoryCache.has(cacheKey)) {
      const cacheItem = this.memoryCache.get(cacheKey);
      
      // 
      if (cacheItem.expiry > Date.now()) {
        this.stats.hits++;
        this.stats.memoryHits++;
        return cacheItem.value;
      } else {
        // 
        this.memoryCache.delete(cacheKey);
      }
    }
    
    // 
    try {
      const storageItem = localStorage.getItem(cacheKey);
      
      if (storageItem) {
        const cacheItem = JSON.parse(storageItem);
        
        // 
        if (cacheItem.expiry > Date.now()) {
          // 
          this.memoryCache.set(cacheKey, cacheItem);
          
          this.stats.hits++;
          this.stats.storageHits++;
          return cacheItem.value;
        } else {
          // 
          localStorage.removeItem(cacheKey);
        }
      }
    } catch (error) {
      console.error('Error reading from localStorage:', error);
    }
    
    // 
    this.stats.misses++;
    return null;
  }

  /**
   * 
   * @param {string} key - 
   * @param {any} value - 
   * @param {number} ttl - 
   * @returns {Promise<void>}
   */
  async set(key, value, ttl) {
    const cacheKey = this.getCacheKey(key);
    const memoryTTL = ttl || this.options.memoryTTL;
    const storageTTL = ttl || this.options.storageTTL;
    
    const now = Date.now();
    
    // 
    const memoryCacheItem = {
      value,
      expiry: now + memoryTTL,
      created: now
    };
    
    this.memoryCache.set(cacheKey, memoryCacheItem);
    
    // 
    try {
      const storageCacheItem = {
        value,
        expiry: now + storageTTL,
        created: now
      };
      
      localStorage.setItem(cacheKey, JSON.stringify(storageCacheItem));
    } catch (error) {
      console.error('Error writing to localStorage:', error);
    }
  }

  /**
   * 
   * @param {string} key - 
   * @returns {Promise<void>}
   */
  async invalidate(key) {
    const cacheKey = this.getCacheKey(key);
    
    // 
    this.memoryCache.delete(cacheKey);
    
    // 
    try {
      localStorage.removeItem(cacheKey);
    } catch (error) {
      console.error('Error removing item from localStorage:', error);
    }
  }

  /**
   * 
   * @returns {Promise<void>}
   */
  async clear() {
    // 
    this.memoryCache.clear();
    
    // 
    try {
      const keysToRemove = [];
      
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key.startsWith(this.options.prefix)) {
          keysToRemove.push(key);
        }
      }
      
      keysToRemove.forEach(key => localStorage.removeItem(key));
    } catch (error) {
      console.error('Error clearing localStorage:', error);
    }
    
    // 
    this.resetStats();
  }

  /**
   * 
   */
  resetStats() {
    this.stats = {
      hits: 0,
      misses: 0,
      memoryHits: 0,
      storageHits: 0
    };
  }

  /**
   * 
   * @returns {Object} - 
   */
  getStats() {
    const total = this.stats.hits + this.stats.misses;
    const hitRate = total > 0 ? (this.stats.hits / total) * 100 : 0;
    
    return {
      ...this.stats,
      total,
      hitRate: hitRate.toFixed(2) + '%',
      memoryItems: this.memoryCache.size,
      memoryUsage: this.estimateMemoryUsage()
    };
  }

  /**
   * 
   * @returns {string} - 
   */
  estimateMemoryUsage() {
    let totalSize = 0;
    
    this.memoryCache.forEach((value, key) => {
      // 
      totalSize += key.length * 2; // 2
      
      // 
      try {
        const jsonSize = JSON.stringify(value).length * 2;
        totalSize += jsonSize;
      } catch (e) {
        // 
        totalSize += 1024; // 1KB
      }
    });
    
    // 
    if (totalSize < 1024) {
      return `${totalSize} B`;
    } else if (totalSize < 1024 * 1024) {
      return `${(totalSize / 1024).toFixed(2)} KB`;
    } else {
      return `${(totalSize / (1024 * 1024)).toFixed(2)} MB`;
    }
  }

  /**
   * 
   * @param {string} baseKey - 
   * @param {Object} params - 
   * @returns {string} - 
   */
  static generateKey(baseKey, params = {}) {
    if (!params || Object.keys(params).length === 0) {
      return baseKey;
    }
    
    // 
    const sortedParams = Object.entries(params)
      .filter(([_, value]) => value !== undefined && value !== null && value !== '')
      .sort(([keyA], [keyB]) => keyA.localeCompare(keyB))
      .map(([key, value]) => `${key}=${value}`)
      .join('&');
    
    return sortedParams ? `${baseKey}?${sortedParams}` : baseKey;
  }
}

export default CacheService;