// HTTP
// HTTP

/**
 * HTTP
 * @param {string} url - URL
 * @param {Object} options - 
 * @param {Object} retryOptions - 
 * @returns {Promise<Object>} - 
 */
export const fetchWithRetry = async (url, options = {}, retryOptions = {}) => {
  const {
    maxRetries = 3,
    retryDelay = 1000,
    timeout = 10000,
    retryCondition = null
  } = retryOptions;

  // 
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), timeout);
  
  const fetchOptions = {
    ...options,
    signal: controller.signal
  };

  let retryCount = 0;
  
  while (true) {
    try {
      const response = await fetch(url, fetchOptions);
      clearTimeout(timeoutId);
      
      // 
      if (!response.ok && retryCount < maxRetries) {
        const shouldRetry = retryCondition 
          ? retryCondition(response)
          : response.status >= 500; // 
          
        if (shouldRetry) {
          retryCount++;
          const delay = retryDelay * Math.pow(2, retryCount - 1);
          console.log(`Retrying request to ${url} in ${delay}ms (${retryCount}/${maxRetries})`);
          await new Promise(resolve => setTimeout(resolve, delay));
          continue;
        }
      }
      
      if (!response.ok) {
        throw new Error(`HTTP error ${response.status}: ${response.statusText}`);
      }
      
      // JSON
      try {
        return await response.json();
      } catch (e) {
        // JSON
        return await response.text();
      }
    } catch (error) {
      clearTimeout(timeoutId);
      
      // 
      if (error.name === 'AbortError') {
        throw new Error(`Request timeout after ${timeout}ms`);
      }
      
      // 
      if (retryCount < maxRetries) {
        retryCount++;
        const delay = retryDelay * Math.pow(2, retryCount - 1);
        console.log(`Retrying request to ${url} in ${delay}ms (${retryCount}/${maxRetries})`);
        await new Promise(resolve => setTimeout(resolve, delay));
      } else {
        throw error;
      }
    }
  }
};

/**
 * CORS
 * @param {string} url - URL
 * @param {Object} options - 
 * @param {Object} retryOptions - 
 * @returns {Promise<Object>} - 
 */
export const fetchWithCorsProxy = async (url, options = {}, retryOptions = {}) => {
  const corsProxy = 'https://cors-anywhere.herokuapp.com/';
  const proxyUrl = `${corsProxy}${url}`;
  
  const proxyOptions = {
    ...options,
    headers: {
      ...options.headers,
      'Origin': window.location.origin
    }
  };
  
  return fetchWithRetry(proxyUrl, proxyOptions, retryOptions);
};

/**
 * RSS feedJSON
 * @param {string} rssUrl - RSS feed URL
 * @param {string} apiKey - RSS2JSON API
 * @returns {Promise<Object>} - JSON
 */
export const parseRssFeed = async (rssUrl, apiKey = '') => {
  const rss2jsonUrl = `https://api.rss2json.com/v1/api.json?rss_url=${encodeURIComponent(rssUrl)}`;
  
  const options = {};
  if (apiKey) {
    options.headers = {
      'Authorization': `ApiKey ${apiKey}`
    };
  }
  
  const response = await fetchWithRetry(rss2jsonUrl, options);
  
  if (response.status !== 'ok') {
    throw new Error(`Failed to parse RSS feed: ${response.message || 'Unknown error'}`);
  }
  
  return response;
};

/**
 * 
 * @param {Object} params - 
 * @returns {string} - 
 */
export const buildQueryString = (params) => {
  if (!params || Object.keys(params).length === 0) {
    return '';
  }
  
  const queryParams = Object.entries(params)
    .filter(([_, value]) => value !== undefined && value !== null && value !== '')
    .map(([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`)
    .join('&');
    
  return queryParams ? `?${queryParams}` : '';
};

export default {
  fetchWithRetry,
  fetchWithCorsProxy,
  parseRssFeed,
  buildQueryString
};